import * as React from 'react';
import {ModalContainer} from '@here/hds-modal';

// eslint-disable-next-line no-new
new ModalContainer();

interface HDSModalContainerProperties extends React.HTMLAttributes<HTMLElement> {
  children?: React.ReactNode;
}

const HDSModalContainer = React.forwardRef((props: HDSModalContainerProperties, ref) => {
  const {children, className, ...rest} = props;
  return (
    <hds-modal-container {...(className && {class: className})} ref={ref} {...rest}>
      {children}
    </hds-modal-container>
  );
});

export default HDSModalContainer;
